<template>
    <div class='login'>
        <div class='login_input' >
            <div class='login_input_ul' >
                <div class='login_log' @click='showUserList'>
                    <p class='login_img'><img src="@/assets/img/login/login.png" alt=""></p>
                    <p class='other_acount'>其他账号</p>
                </div>
                <div class='form_input'>
                    <p class='form_input_p'><label>手机号：</label><input type="number" placeholder="请输入手机号码" v-model="sendForm.phone" :maxlength="15"><span class='verif_code'>获取验证码</span></p>
                    <p class='form_input_p'><label>验证码：</label><input type="number"  placeholder="请输入验证码" v-model="sendForm.code" :maxlength="6"></p>
                    <p class='newusertext'>新用户获取验证码验证后即登陆成功</p>
                    <p class='login_button'><button @click='login'>登陆游戏</button></p>
                    <p class='protocol'><i :class='choseProtocoel?"chose_i":"no_chosese"' @click='handleProtocoel'></i><span>梵世科技网络服务协议</span></p> 
                </div>  
                <div class='other_logintype'>
                    <p class='other_logintype_title'>
                        <i></i>
                        <span class='other_logintype_text'>其他登陆</span>
                        <i></i>
                    </p>
                    <p class='other_logintype_chose'>
                        <img src="@/assets/img/login/login_QQ.png" alt="">
                        <img src="@/assets/img/login/login_watch.png" alt="">
                    </p>
                </div>
            </div>
           
        </div>
        <div class='login_input_user' v-if='userListShow'>
            <div class='header_close'>  <img src='@/assets/img/login/close.png' @click='closeUserList'></div>
            <ul class='login_input_ul ' >
              <li class='user_list' v-for='(item,index) in userList' :key='index' >
                 <p  @click='chosePhone(item,index)'>{{item.userName}}</p>
                 <p @click='chosePhone(item,index)'>{{item.phone}}</p>
                <img src="@/assets/img/login/small_close.png" alt="" @click='userListDel(item,index)'>
              </li>
            </ul>
           
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            choseProtocoel:false,
            userListShow:false,
            sendForm:{
                phone:null,
                code:null,
            },
            userList:[
                {userName:'feng1',phone:'15919304717'},
                {userName:'feng2',phone:'15919304717'},
                 {userName:'feng3',phone:'15919304717'},
                {userName:'feng4',phone:'15919304717'},
                 {userName:'feng5',phone:'15919304717'},
                {userName:'feng6',phone:'15919304717'},
                 {userName:'feng7',phone:'15919304717'},
                {userName:'feng8',phone:'15919304717'},
                 {userName:'feng9',phone:'15919304717'},
                {userName:'feng10',phone:'15919304717'},
            ],
        }
    },
    mounted(){
        
    },
    methods:{
        handleProtocoel(){
            this.choseProtocoel=!this.choseProtocoel;
        },
        userListDel(item,index){
            this.userList.splice(index,1);
        },
        showUserList(){
            this.userListShow = true;
        },
        closeUserList(){
            this.userListShow = false;
        },
        verificationSend(){
            let regPhone = /^1\d{10}$/;
            if(!this.sendForm.phone){
                this.$Toast('请输入手机号码！')
                return false;
            }else if(!regPhone.test(this.sendForm.phone)){
                 this.$Toast('请输入正确的手机号码！')
                 return false;

            }else if(!this.sendForm.code){
                this.$Toast('请输入验证码！')
                return false;
            }else{
                return true;
            }
        },
        login(){
            if(this.verificationSend()){
                console.log(888)
                this.$router.push({path:'/newStep'})
            }
        },
        chosePhone(item,index){
            this.sendForm.phone = item.phone;
            this.userListShow = false;
        },
    },
}
</script>
<style lang="less" scoped>
    @import '../../common/css/pulic.less';
    .login{
        width:100%;
        height:100%;
        background: url('../../assets/img/background2.png') no-repeat no-repeat 0 0;
        background-size:100% 100%; 
        position:relative;
        .login_input{
            width:620px;
            height:710px;
            background-color:rgba(0,0,0,0.7) ;
            position:absolute;
            top:50%;
            left:50%;
            transform: translate(-50% ,-50%);
            padding:26px 16px;
            .box-sizing(@border-box);
           
            z-index:1000;
            .login_input_ul{
                width:100%;
                height:100%;
                .login_log{
                    display:flex;
                    justify-content:space-between;
                    .login_img{
                        width:219px;
                        height:83px;
                        img{
                            display:block;
                            width:100%;
                        }
                    }
                    .other_acount{
                        font-size:28px;
                        color:#0cebfe;
                        width:164px;
                        height:30px;
                        border-bottom:2px #0cebfe solid;
                        line-height: 30px;
                        padding-left: 40px;
                        .box-sizing(@border-box);
                        background:url('../../assets/img/login/othen_change.png') 0 0 no-repeat;
                        background-size:40px;
                        margin-top:25px;
                    }

                }
                .form_input{
                    width:465px;
                    height:403px;
                    margin:26px auto 0 auto;
                    .form_input_p{
                        width:462px;
                        height:70px;
                        background:rgba(114,118,127,0.9);
                        border-radius: 4px 4px;
                        display:flex;
                        justify-content: flex-start;
                        margin-top:26px;
                        label{
                            color:white;
                            font-sze:23px;
                            display:block;
                            height:100%;
                            line-height: 70px;
                            width:100px;
                            flex-shrink: 0;
                            text-align: right;
                        }
                        input{
                            display:block;
                            background:rgba(0,0,0,0);
                            width:212px;
                            display:block;
                        }
                        .verif_code{
                            border:1px #00ebff solid;
                            border-radius:4px 4px;
                            -o-border-radius:4px 4px;
                            -moz-border-radius:4px 4px;
                            -ms-border-radius:4px 4px;
                            -webkit-border-radius:4px 4px;
                            color:#00ebff;
                            height:37px;
                            width:134px;
                            margin-right:9px;
                            flex-shrink: 0;
                            line-height: 37px;
                            margin-top:18px;
                            padding:0 6px;
                            .box-sizing(@border-box);
                            font-size:23px;

                        }
                        ::-webkit-input-placeholder { /* WebKit, Blink, Edge */
                            color:    #bbbbbb;
                        }
                        :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
                        color:    #bbbbbb;
                        }
                        ::-moz-placeholder { /* Mozilla Firefox 19+ */
                        color:    #bbbbbb;
                        }
                        :-ms-input-placeholder { /* Internet Explorer 10-11 */
                        color:    #bbbbbb;
                        }
                    }
                    .newusertext{
                        width:100%;
                        height:23px;
                        line-height: 23px;
                        color:#ff0000;
                        margin-top:19px;
                        margin-bottom:24px;
                        text-align:center;
                    }
                    .login_button{
                        width:100%;
                        height:91px;
                        margin-bottom:18px;
                        button{
                            display:block;
                            width:238px;
                            height:100%;
                            line-height: 91px;
                            color:white;
                            text-align: center;
                            background:#0cebfe;
                            font-size:38px;
                            border-radius: 5px 5px;
                            -o-border-radius: 5px 5px;
                            -ms-border-radius: 5px 5px;
                            -moz-border-radius: 5px 5px;
                            -webkit-border-radius: 5px 5px;
                            margin:0 auto;
                            text-shadow:2px 2px 2px #3daded ;
                        }
                    }
                    .protocol{
                        width:100%;
                        height:28px;
                        line-height: 28px;
                        color:white;
                        display:flex;
                        justify-content:center;
                        i{
                            font-style: normal;
                            display:block;
                            width:26px;
                            height:26px;
                            margin-top:1px;
                            margin-right:9px;
                        }
                        .no_chosese{
                            background:white;
                            border-radius: 50% 50%;
                            -o-border-radius: 50% 50%;
                            -ms-border-radius: 50% 50%;
                            -moz-border-radius: 50% 50%;
                            -webkit-border-radius:50% 50%;
                        }
                        .chose_i{
                            background: url('../../assets/img/login/chose_protocol.png') 0 0 no-repeat ;
                            background-size:100% 100%;
                        }
                    }
                }
               
                .other_logintype{
                    width:100%;
                    height:auto;
                    .other_logintype_title{
                        width:100%;
                        height:30px;
                        line-height: 30px;
                        font-size:30px;
                        color:white;
                        display:flex;
                        margin-bottom:29px;
                        i{
                            display:block;
                            flex:1;
                            width:217px;
                            height:4px;
                            background:#515358;
                            margin-top:13px;
                        }
                        .other_logintype_text{
                            width:124px;
                            flex-shrink:0;
                            margin-left:3px;
                            margin-right:3px;
                            display:block;
                        }
                       
                    }
                    .other_logintype_chose{
                        width:100%;
                        height:auto;
                        display:flex;
                        justify-content:center;
                        img{
                            display:block;
                            width:88px;
                            height:88px;
                            
                        }
                        & :nth-child(1){
                            margin-right:62px;
                        }
                    }
                }

            }
        }
        .login_input_user{
            width:620px;
            height:710px;
            background-color:rgba(0,0,0,0.7) ;
            position:absolute;
            top:50%;
            left:50%;
            transform: translate(-50% ,-50%);
            padding:26px 16px;
           .box-sizing(@border-box);
            z-index:2000;
            .header_close{
                    width:100%;
                    height:80px;
                    margin-bottom:55px;
                    img{
                        display:block;
                        width:auto;
                        height:76px;
                        float:right;
                    }
                }
            .login_input_ul{
                width:100%;
                height:calc(100% - 135px);
                overflow-y: auto;
                
                .user_list{
                    height:70px;
                    width:549px;
                    color:white;
                    font-size:25px;
                    display:flex;
                    justify-content: space-between;
                    background:#6a6a6a;
                    line-height: 70px;
                    margin:0 auto 22px auto;
                    position:relative;
                    img{
                        display:block;
                        width:46px;
                        height:46px;
                        margin-top:12px;
                        margin-right:18px;
                    }
                    & :nth-child(1){
                        margin-left:18px;
                    }
                    &::before{
                        content: '';
                        width:8px;
                        height:40px;
                        background:#30ff00;
                        font-weight:bold;
                        font-size:40px;
                        position:absolute;
                        top:15px;
                        left:-4px;

                    }
                }

            }
        }
    }
</style>


